@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=JetBrains+Mono&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@500;700&display=swap);
.shortTableRow > td:first-child {
  background-color: #8f8f8f;
  padding: 2px 60px 2px 10px;
}

.shortTableRow > td:last-child {
  background-color: #8f8f8f;
  padding: 2px 10px 2px 2px;
}

.short-table {
  border-collapse: seperate;
  border-spacing: 0;
  border: 1px solid black;
  border-radius: 15px 15px 0 0;
  overflow: hidden;
}

.short-table-title {
  font-family: "Open Sans", sans-serif;
  background-color: #c4c4c4;
  border-radius: 15px 15px 0 0;
}

.th-two > th:first-child {
  font-family: "Roboto", sans-serif;
  font-weight: 200;
  background-color: #8f8f8f;
  padding: 2px 60px 2px 10px;
  color: black;
  text-align: left;
  border-top: 1px solid black;
}

.th-two > th:last-child {
  font-family: "Roboto", sans-serif;
  font-weight: 200;
  background-color: #8f8f8f;
  padding: 2px 10px 2px 2px;
  color: black;
  text-align: left;
  border-top: 1px solid black;
}

.short-table-footer > td {
  padding-bottom: 10px;
  background-color: #8f8f8f;
}

p {
  color: #359454;
  font-weight: 700;
  font-size: 20px;
  margin: 0 0 10px 0;
}

button {
  background-color: #8f8f8f;
  border: 3px solid black;
  border-radius: 10px;
  padding: 5px;
  width: 70%;
  font-weight: 600;
  margin-top: 20px;
  margin-bottom: 20%;
  outline: none;
}

h2 {
  font-family: "JetBrains Mono", monospace;
  margin: 0;
}

h3 {
  font-family: "Open Sans", sans-serif;
  margin: 0;
}

.pagea-flex {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin-top: 20px;
  justify-content: space-between;
}
.pagea-account {
  align-self: flex-start;
  margin-left: 3%;
}
.pagea-table {
  align-self: flex-start;
  justify-self: center;
  margin-right: 30%;
}

.long-table-row > td {
  font-family: "Roboto", sans-serif;
  text-align: left;
  padding: 8px;
  border: hidden;
  background-color: #9e9e9e;
  font-weight: 500;
}

.long-table {
  border-collapse: collapse;
  border-spacing: 0;
  border: none;
  border-radius: 15px 15px 15px 15px;
  background-color: #9e9e9e;
  overflow: hidden;
  position: absolute;
  margin: 1%;
  width: 90%;
}

.long-tablehead-rw > th {
  font-family: "Roboto", sans-serif;
  text-align: left;
  padding: 8px;
  background-color: #9e9e9e;
  font-weight: 700;
}

@media only screen and (max-width: 768px) {
  .long-table,
  .long-th,
  .long-tb,
  .long-tablehead-rw > th,
  .long-td,
  .long-tablehead-rw,
  .long-table-row {
    display: block;
  }
  .long-tablehead-rw {
    position: absolute;
    top: -9999px;
    left: -9999px;
  }
  .long-table-row {
    border: 1px solid #ccc;
    font-weight: 400;
  }
  .long-td {
    border: none;
    border-bottom: 1px solid #eee;
    position: relative;
    padding-left: 200px;
    margin-left: 150px;
  }
  .long-td::before {
    font-family: "Roboto", sans-serif;
    font-weight: 500;
    position: absolute;
    top: 12px;
    left: 6px;
    width: 200px;
    padding-right: 40px;
    white-space: nowrap;
    margin-left: -150px;
  }
  .long-table-row > td {
    font-weight: 400;
    font-family: "Roboto", sans-serif;
  }
  .long-td:nth-of-type(1):before {
    content: "Name";
  }
  .long-td:nth-of-type(2):before {
    content: "Stock";
  }
  .long-td:nth-of-type(3):before {
    content: "Price";
  }
  .long-td:nth-of-type(4):before {
    content: "Market Cap";
  }
}

h4 {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
}

